import React from 'react';
import QuestionListComponent from './QuestionList.component';

export default class QuestionPageComponent extends React.Component {
  constructor(props) {
    super(props);
    this.router = {
      personal: this.renderPersonal,
      address: this.renderAddress,
      documents: this.renderDocument,
      relatives: this.renderRelatives,
      employment: this.renderEmployment,
      education: this.renderEducation,
    };
  }

  renderQuestionList() {
    let name = this.props.questionPage.name;
    return this.props.questionPage.questionLists
    .map((questionList, i) =>[
      <QuestionListComponent
        key={i} name={name} questionList={questionList}
        onChange={(questionIndex, value) => {
          this.props.onChange(i, questionIndex, value)
        }}
        formSubmitted={this.props.formSubmitted}
      />,
      i != 0 ?
        <div className="row" style={{textAlign: "right"}}>
          <a style={{
              textDecoration: "underline",
              lineHeight: "1.6",
              marginRight: "20px"
            }}
             onClick={() => this.props.onRemoveQuestionList(i)}
          ></a>
        </div> : "",
      <br />
    ]);
  }

  renderPersonal() {
    return (
      <div className="query-block" style={{width: "700px", marginBottom: "30px"}}>
        <form id={`${this.props.questionPage.name}-query-form`}>
          <div className="quote-form primary-theme">
            <div className="quote-form-title"></div>
            {this.renderQuestionList()}
          </div>
        </form>
      </div>
    );
  }

  renderDocument() {
    return (
      <div className="query-block" style={{width: "400px", marginBottom: "30px"}}>
        <form id={`${this.props.questionPage.name}-query-form`}>
          <div className="quote-form primary-theme">
            <div className="quote-form-title"></div>
            {this.renderQuestionList()}
          </div>
        </form>
      </div>
    );
  }

  renderAddress(){
    return (
      <div className="query-block" style={{width: "700px", marginBottom: "30px"}}>
        <form id={`${this.props.questionPage.name}-query-form`}>
          <div className="quote-form primary-theme">
            <div className="quote-form-title"></div>
            {this.renderQuestionList()}
          </div>
        </form>
      </div>
    );
  }

  renderRelatives() {
    return (
      <div className="query-block" style={{width: "400px"}}>
        <form id={`${this.props.questionPage.name}-query-form`}>
          <div className="quote-form primary-theme">
            <div className="quote-form-title"></div>
            <div className="row">
              <div className="col-md-3">
                <div className="row">
                  <button className="btn btn-sm btn-primary" type="button" id="add-relative"
                          onClick={() => this.props.onAddQuestionList()}>
                  </button>
                </div>
              </div>
            </div>
            <br />
            {this.renderQuestionList()}
          </div>
        </form>
      </div>
    );
  }

  renderEmployment(){
    return (
      <div className="query-block" style={{width: "400px"}}>
        <form id={`${this.props.questionPage.name}-query-form`}>
          <div className="quote-form primary-theme">
            <div className="quote-form-title"></div>
            <div className="row">
              <div className="col-md-3">
                <div className="row">
                  <button className="btn btn-sm btn-primary" type="button" id="add-relative"
                          onClick={() => this.props.onAddQuestionList()}>
                  </button>
                </div>
              </div>
            </div>
            <br />
            {this.renderQuestionList()}
          </div>
        </form>
      </div>
    );
  }

  renderEducation(){
    return (
      <div className="query-block" style={{width: "400px"}}>
        <form id={`${this.props.questionPage.name}-query-form`}>
          <div className="quote-form primary-theme">
            <div className="quote-form-title"></div>
            <div className="row">
              <div className="col-md-3">
                <div className="row">
                  <button className="btn btn-sm btn-primary" type="button" id="add-relative"
                          onClick={() => this.props.onAddQuestionList()}>
                  </button>
                </div>
              </div>
            </div>
            <br />
            {this.renderQuestionList()}
          </div>
        </form>
      </div>
    );
  }

  render() {
    /*switch (this.props.questionPage.name) {
      case "personalInfo":
        return this.renderPersonal();
      case "documentInfo":
        return this.renderDocument();
      case "relativesInfo":
        return this.renderRelatives();
    }*/
    let renderFun = this.router[this.props.questionPage.name].bind(this);
    if(renderFun) {
      return renderFun();
    }
    return <div></div>;
  };
}


