import React from 'react';
import QUESTIONNAIRE_SCHEMA from '../../data';
import Questionnaire from '../proto/Questionnaire';
import Person from './Person.component';
import * as _ from 'lodash';
import ReviewPageComponent from './ReviewPage.component';

export default class QuestionnaireComponent extends React.Component {
  constructor(props) {
    super(props);
    let questionnaire = new Questionnaire(QUESTIONNAIRE_SCHEMA, props.includedBlocks);
    let isValidate = QuestionnaireComponent._validateQuestionStatus(questionnaire, 0, 0);
    this.state = {
      currentPersonIndex: 0,
      currentPageIndex: 0,
      questionnaire: questionnaire,
      validation: {
        submitted: false,
        isValidate: isValidate,
      }
    }
  }

  static _validateQuestionStatus(questionnaire, currentPersonIndex, currentPageIndex) {
    let questionListArr = questionnaire.getPerson(currentPersonIndex).getQuestionPage(currentPageIndex).getQuestionListArr();
    let isValidate = true;
    let checkQuestionList = function (questions) {
      let qlIsValidate = true;
      _.each(questions, q => {
        if (!q.isValid()) {
          qlIsValidate = false;
          return false;
        }
      });
      return qlIsValidate;
    };
    _.each(questionListArr, ql => {
      let qlIsValid = checkQuestionList(ql.getQuestionArr());
      if (!qlIsValid) {
        isValidate = false;
        return false;
      }
    });
    return isValidate;
  }

  addQuestionList() {
    const questionnaire = _.clone(this.state.questionnaire);
    const currentPerson = questionnaire.getPerson(this.state.currentPersonIndex);
    const currentPage = currentPerson.getQuestionPage(this.state.currentPageIndex);
    currentPage.addQuestionList();
    this.setState({
      questionnaire: questionnaire
    });
  }

  removeQuestionList(i) {
    const questionnaire = _.clone(this.state.questionnaire);
    const currentPerson = questionnaire.getPerson(this.state.currentPersonIndex);
    const currentPage = currentPerson.getQuestionPage(this.state.currentPageIndex);
    currentPage.removeQuestionList(i);
    this.setState({
      questionnaire: questionnaire
    });
  }

  handleChange(listIndex, questionIndex, answer) {
    const questionnaire = _.clone(this.state.questionnaire);
    const currentPerson = questionnaire.getPerson(this.state.currentPersonIndex);
    const currentPage = currentPerson.getQuestionPage(this.state.currentPageIndex);
    const question = currentPage.getQuestionList(listIndex).getQuestion(questionIndex);
    question.setAnswer(answer);
    this.setState({
      questionnaire: questionnaire
    });
  }

  goBack(e) {
    e.preventDefault();
    let questionnaire = this.state.questionnaire;
    let currentPersonIndex = this.state.currentPersonIndex;
    let currentPageIndex = this.state.currentPageIndex;
    currentPageIndex--;
    this.setState({
      currentPageIndex: currentPageIndex,
      validation: {
        submitted: false,
        isValidate: QuestionnaireComponent._validateQuestionStatus(questionnaire, currentPersonIndex, currentPageIndex)
      }
    });
  }

  goNext(e) {
    e.preventDefault();
    let validation = _.clone(this.state.validation);
    let currentPersonIndex = this.state.currentPersonIndex;
    let currentPageIndex = this.state.currentPageIndex;
    let questionnaire = this.state.questionnaire;
    validation.submitted = true;
    validation.isValidate = QuestionnaireComponent._validateQuestionStatus(questionnaire, currentPersonIndex, currentPageIndex);
    if (!validation.isValidate) {
      this.setState({
        validation: validation
      });
      return;
    }
    $.ajax({
      url: '/update',
      method: 'POST',
      data: {
        oper: "addInfo",
        checklist: this.state.questionnaire.toUploadData(),
        caseid: this.props.caseId
      }
    }).done(() => {
      currentPageIndex++;
      validation.submitted = false;
      this.setState({
        currentPageIndex: currentPageIndex,
        validation: validation
      });
    });
  }

  goToPage(index){
    this.setState({
      currentPageIndex: index
    });
  }

  renderNavigator() {
    const currentPageIndex = this.state.currentPageIndex;
    const pageNum = this.state.questionnaire.getPerson(0).getQuestionPagesArr().length;
    let navigators = [];
    if (currentPageIndex != 0)
      navigators.push(
        <button key={"preBtn"} id="abc" className="btn btn-lg btn-primary" style={{float: "left"}}
                onClick={(e) => this.goBack(e)}>
          
        </button>);
    if (currentPageIndex < pageNum)
      navigators.push(
        <button key={"nxtBtn"} className="btn btn-lg btn-primary" style={{float: "right"}}
                onClick={(e) => this.goNext(e)}>
          
        </button>);
    else
      navigators.push(
        <button key={"rvBtn"} className="btn btn-lg btn-primary next-step" style={{float: "right"}}
                onClick={(e) => this.handleSubmit()}>
          
        </button>);
    return navigators;
  }

  handleSubmit(){
    $.ajax({
      url: '/update',
      method: 'POST',
      data: {
        oper: "submitCase",
        caseid: this.props.caseId,
        sellerid: this.props.sellerId,
      }
    }).done(function(){
      window.location = "/mycases";
    });
  }

  render() {
    let currentPerson = this.state.questionnaire.getPerson(this.state.currentPersonIndex);
    let currentPageIndex = this.state.currentPageIndex;
    return currentPageIndex === currentPerson.getQuestionPagesArr().length ?
      (<div>
        <ReviewPageComponent
          onSubmit={() => this.handleSubmit()}
          sellerName={this.props.sellerName}
        />
        <div className="row">
          {this.renderNavigator()}
        </div>
      </div>) :
      (<div>
        <Person
          person={currentPerson} currentPage={this.state.currentPageIndex}
          onAddQuestionList={() => this.addQuestionList()}
          onRemoveQuestionList={(i) => this.removeQuestionList(i)}
          onChange={(listIndex, questionIndex, value) => this.handleChange(listIndex, questionIndex, value)}
          formSubmitted={this.state.validation.submitted}
        />
        <div className="row">
          {this.renderNavigator()}
        </div>
      </div>);
  };
}


