import Question from "./Question";

export default class QuestionList {
  constructor(schema) {
    this.schema = schema;
    this.questions = QuestionList._generateQuestionList(this.schema);
  }

  static _generateQuestionList(schema) {
    let titles = schema.titles;
    return titles.map(title => {
      let questionData = schema[title];
      return new Question(questionData);
    });
  }

  getQuestion(i){
    return this.questions[i];
  }

  getQuestionArr(){
    return this.questions;
  }

  toUploadData(){
    const questions = this.questions;
    return questions.map(q => q.toUploadData());
  }
}


