import Person from './Person';

export default class Questionnaire{
  constructor(schema, blocks) {
    this.schema = schema;
    this.blocks = blocks;
    this.people = [new Person(schema, blocks)];
  }

  getPersonArr(){
    return this.people;
  }

  getPerson(i){
    return this.people[i];
  }

  addPerson(){
    this.people.push(new Person(this.schema, this.blocks));
  }

  removePerson(i){
    this.people.splice(i, 1);
  }

  toUploadData(){
    return {
      applicants: this.people.map(p => p.toUploadData()),
      include: this.blocks
    };
  }
}


