export default class Question{
  constructor(schema) {
    this.title = schema.title;
    this.type = schema.type;
    this.className = schema.className;
    this.id = this.title.split(" ").map((t) => t.toLowerCase()).join("-");
    this.choices = schema.choices || [];
    this.answer = "";
    this.required = schema.required || false;
    this.format = schema.format;
    this.validate = !this.required;
  }

  checkValidate(){
    if(this.required){
      if(!this.answer){
        this.validate = false;
        return;
      }
      if(!this.format){
        this.validate = true;
        return;
      }
      let regex= new RegExp(this.format[0], this.format[1] || "");
      this.validate = regex.test(this.answer);
      return;
    }
    this.validate = true;
  }
  isValid(){
    return this.validate;
  }
  setAnswer(answer){
    if( typeof answer === 'string')
      this.answer = answer.trim();
    else
      this.answer = answer;
    this.checkValidate();
  }
  getAnswer(){
    return this.answer;
  }

  toUploadData(){
    const title = this.title;
    const answer = this.answer;
    return {
      title,
      answer,
    };
  }
}


