import QuestionPage from './QuestionPage';

export default class Person{
  constructor(schema, blocks){
    this.blocks = blocks;
    this.questionPages = Person._generateQuestionPages(schema, blocks);
  }

  static _generateQuestionPages(schema, blocks){
    let defaultBlocks = schema['blocks'];
    let pBlocks = blocks || defaultBlocks;
    return pBlocks.map(block => new QuestionPage(block, schema[block]));
  }

  getQuestionPagesArr(){
    return this.questionPages;
  }

  getQuestionPage(i){
    return this.questionPages[i];
  }

  toUploadData(){
    return this.questionPages.map(qp => qp.toUploadData());
  }
}


