import React from 'react';

export default class ReviewPageComponent extends React.Component{
  render(){
    return (
      <div style={{width: "718px",}} id="responseModal">
        <div className="modal-header">
          <div className="caption">
            <img src="static/images/check%20mark.png" />
              
          </div>
        </div>
        <div className="modal-body">
          <p>:,48</p>
          <div className="order-info-block">
            <p>:{this.props.sellerName}</p>
          </div>
          <p>,: <span style={{color: "#fc7777"}}>(800) 685-6947</span></p>
        </div>
      </div>
    );
  }
}


