import React from 'react';
import QuestionComponent from './Question.component';

export default class QuestionListComponent extends React.Component {
  constructor() {
    super();
  }

  renderQuestion(i) {
    let questions = this.props.questionList.questions;
    return <QuestionComponent
              key={questions[i].id}
              formSubmitted={this.props.formSubmitted} question={questions[i]}
              onChange={(value) => this.props.onChange(i, value)}
            />;
  }

  renderPersonal() {
    return (
      <div className="question-list">
        <div className="row">
          <div className="col-md-6">
            <div className="row">
              <div className="col-md-12">
                <div className="sub-block-title"></div>
              </div>
            </div>
            <div className="row">
              {this.renderQuestion(0)}
              {this.renderQuestion(1)}
            </div>
            <div className="row">
              {this.renderQuestion(2)}
              {this.renderQuestion(3)}
            </div>
            <div className="row">
              {this.renderQuestion(4)}
              {this.renderQuestion(5)}
            </div>
            <div className="row">
              {this.renderQuestion(6)}
              {this.renderQuestion(7)}
            </div>
          </div>
          <div className="col-md-6">
            <div className="row">
              <div className="sub-block-title"></div>
            </div>
            <div className="row">
              {this.renderQuestion(8)}
            </div>
            <div className="row">
              {this.renderQuestion(9)}
              {this.renderQuestion(10)}
            </div>
          </div>
        </div>
      </div>
    );
  }

  renderAddress(){
    return(
      <div className="question-list">
        <div className="row">
          <div className="col-md-6">
            <div className="row">
              <div className="col-md-12">
                <div className="sub-block-title"></div>
              </div>
            </div>
            <div className="row">
              {this.renderQuestion(0)}
              {this.renderQuestion(1)}
            </div>
            <div className="row">
              {this.renderQuestion(2)}
              {this.renderQuestion(3)}
            </div>
            <div className="row">
              {this.renderQuestion(4)}
            </div>
          </div>
          <div className="col-md-6">
            <div className="row">
              <div className="sub-block-title"></div>
            </div>
            <div className="row">
              {this.renderQuestion(5)}
              {this.renderQuestion(6)}
            </div>
            <div className="row">
              {this.renderQuestion(7)}
              {this.renderQuestion(8)}
            </div>
            <div className="row">
              {this.renderQuestion(9)}
              {this.renderQuestion(10)}
            </div>
          </div>
        </div>
      </div>
    );
  }

  renderDocument() {
    return (
      <div className="question-list">
        <div className="row">
          {this.renderQuestion(0)}
        </div>
        <div className="row">
          {this.renderQuestion(1)}
        </div>

      </div>
    );
  }

  renderRelatives() {
    return (
      <div className="question-list">
        <div className="row">
          {this.renderQuestion(0)}
          {this.renderQuestion(1)}
        </div>
        <div className="row">
          {this.renderQuestion(2)}
        </div>
        <div className="row">
          {this.renderQuestion(3)}
        </div>
      </div>
    );
  }

  renderEmployment() {
    return (
      <div className="question-list">
        <div className="row">
          {this.renderQuestion(0)}
          {this.renderQuestion(1)}
        </div>
        <div className="row">
          {this.renderQuestion(2)}
        </div>
        <div className="row">
          {this.renderQuestion(3)}
          {this.renderQuestion(4)}
        </div>
        <div className="row">
          {this.renderQuestion(5)}
          {this.renderQuestion(6)}
        </div>
        <div className="row">
          {this.renderQuestion(7)}
          {this.renderQuestion(8)}
        </div>
      </div>
    );
  }

  renderEducation() {
    return (
      <div className="question-list">
        <div className="row">
          {this.renderQuestion(0)}
          {this.renderQuestion(1)}
        </div>
        <div className="row">
          {this.renderQuestion(2)}
          {this.renderQuestion(3)}
        </div>
        <div className="row">
          {this.renderQuestion(4)}
          {this.renderQuestion(5)}
        </div>
        <div className="row">
          {this.renderQuestion(6)}
          {this.renderQuestion(7)}
        </div>
      </div>
    );
  }

  render() {
    switch (this.props.name) {
      case "personal":
        return this.renderPersonal();
      case "address":
        return this.renderAddress();
      case "documents":
        return this.renderDocument();
      case "relatives":
        return this.renderRelatives();
      case "employment":
        return this.renderEmployment();
      case "education":
        return this.renderEducation();
    }
    return <div></div>;
  };
}


