import QuestionList from './QuestionList';

export default class QuestionPage {
  constructor(name, schema) {
    this.name = name;
    this.schema = schema;
    this.questionLists = [new QuestionList(this.schema)];
  }

  addQuestionList() {
    console.log("list is added");
    this.questionLists.push(new QuestionList(this.schema));
  }

  getQuestionList(i) {
    return this.questionLists[i];
  }

  getQuestionListArr() {
    return this.questionLists;
  }

  removeQuestionList(i) {
    this.questionLists.splice(i, 1);
  }

  toUploadData(){
    const name = this.name;
    const questionLists = this.questionLists.map(ql => ql.toUploadData());
    return {
      name,
      questionLists,
    }
  }
}



