import React from 'react';
import ReactDOM from 'react-dom';
import QuestionnaireComponent from './questionnaire/react.component/Questionnaire.component';
import $ from 'jquery';

let caseId = "";
let sellerId = "";
let sellerName = "";
let includedBlocks;

$('.service-application').click(() => {
  console.log("");
  let tokenizeUrl = window.location.href.split("/");
  let serviceId = tokenizeUrl[tokenizeUrl.length - 1];
  if($('#account-comp').length > 0){
    if(!caseId) {
      $.ajax({
        url: "/update",
        method: "POST",
        data: {
          oper: "addcase",
          serviceid: serviceId
        }
      }).done((data) => {
        caseId = data.caseid;
        sellerId = data.sellerid;
        sellerName = data.sellerName;
        includedBlocks = data.checklist.include;
        ReactDOM.render(
          <QuestionnaireComponent
            caseId={caseId} sellerId={sellerId} sellerName={sellerName}
            includedBlocks={includedBlocks}
          />,
          document.getElementById('query-tab-content'),
          () => $('#quote-modal').modal('toggle')
        );
      });
    }
    else
      ReactDOM.render(
        <QuestionnaireComponent
          caseId={caseId} sellerId={sellerId} sellerName={sellerName}
          includedBlocks={includedBlocks}
        />,
        document.getElementById('query-tab-content'),
        () => $('#quote-modal').modal('toggle')
      );
  }
  else{
    $('.login-warning').css('display', 'block');
    $('#loginModal').modal('toggle');
  }
});





