import React from 'react';
import QuestionPageComponent from './QuestionPage.component';

export default class PersonComponent extends React.Component{

  render(){
    let currentPage = this.props.currentPage;
    //let currentQuestionPageObj = this.props.person.getQuestionPage(currentPage);
    let questionPageArr = this.props.person.getQuestionPagesArr();
    let questionPageHTMLArr =
      questionPageArr.map((qp, i) =>
        <QuestionPageComponent
          key={`queryPage${i}`}
          questionPage={qp}
          onAddQuestionList={() => this.props.onAddQuestionList()}
          onRemoveQuestionList={(i) => this.props.onRemoveQuestionList(i)}
          onChange={(listIndex, questionIndex, value) => this.props.onChange(listIndex, questionIndex, value)}
          formSubmitted={this.props.formSubmitted}
        />
      );
    return(
      <div>
        {questionPageHTMLArr.filter((p, i) => i === currentPage)}
      </div>
    );
  }
}


