import React from 'react';
import classNames from 'classnames';
import Cleave from 'cleave.js/react';

export default class QuestionComponent extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      pristine: true,
      value: ""
    };
  }

  setDirty(){
    if(this.state.pristine)
      this.setState({
        pristine: false
      });
  }

  render(){
    let input = "";
    let question = this.props.question;
    let showWarning = ((!this.state.pristine) || this.props.formSubmitted) && !question.isValid();
    switch(question.type){
      case "select":
        let options = question.choices.map((value) => <option key={value} value={value}>{value}</option>);
        input =
          <div className="select">
            <select
              id={question.id} className={showWarning ? "warning" : ""}
              name="gender"
              onChange={(e) => this.props.onChange(e.target.value)}
              onBlur={(e) => this.setDirty()} value={question.getAnswer()}
            >
              <option value="" style={{display: "none"}}>{question.title}</option>
              {options}
            </select>
            <div className="select-arrow"></div>
          </div>;
        break;
      case "date-mdy":
        input =
          <Cleave
            placeholder={question.title} onBlur={(e) => this.setDirty()}
            id={question.id} className={showWarning ? "warning" : ""}
            options={{date: true, datePattern: ['m', 'd', 'Y']}}
            onChange={(e) => this.props.onChange(e.target.value)}
            value={question.getAnswer()}
          />;
        break;
      case "textarea":
        input =
          <textarea
            placeholder={question.title}
            onChange={(e) => this.props.onChange(e.target.value)}
            style={{
              height: "75px",
              border: "0",
              backgroundColor: "#F2F2F2",
              padding: "10px 10px"
            }} onBlur={() => this.setDirty()}
            value={question.getAnswer()}
          />;
        break;
      case "text":
      default:
        input =
          <input
            id={question.id} className={showWarning ? "warning" : ""}
            type={question.type}
            placeholder={question.title} onBlur={(e) => this.setDirty()}
            onChange={(e) => this.props.onChange(e.target.value)}
            value={question.getAnswer()}
          />;
    }
    return (
      <div className={classNames(question.className)}>
        {input}
      </div>
    );
  }
}


